import { matchPath } from "react-router-dom"
import { WATCHLIST_CHAT_ID_PARAM } from "@rp/common/settings"
import { WATCHLIST_ID_ROUTE, WATCHLIST_NEW } from "@rp/react/web/router"

export const extractRouteParams = (pathname: string, search: string) => {
  const searchParams = new URLSearchParams(search)
  const isNewWatchlistPath = matchPath(WATCHLIST_NEW, pathname)
  const isWatchlist = !isNewWatchlistPath && matchPath(WATCHLIST_ID_ROUTE, pathname)
  const watchlistChatId = isWatchlist ? searchParams.get(WATCHLIST_CHAT_ID_PARAM) : undefined

  const watchlistId = isWatchlist
    ? pathname.split("/").pop() // Adjust depending on route structure
    : undefined

  return { isWatchlist, watchlistId, watchlistChatId }
}

export const toDynamicRoute = (path: string, params: Record<string, string | undefined>) =>
  Object.entries(params).reduce((acc, [key, value]) => acc.replace(`/${value}`, `/:${key}`), path)
